.card input[type=radio] {
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: 0;
  background: #eee;
  height: 16px;
  width: 16px;
  border: 1px solid white;
}
.card input[type=radio]:checked {
  background: #59c1d5;
  color: #59c1d5 !important;
}
.card input[type=radio]:hover {
  filter: brightness(90%);
}
.card input[type=radio]:disabled {
  background: #393938;
  opacity: 0.6;
  pointer-events: none;
}
.card input[type=radio]:after {
  content: "";
  position: relative;
  left: 40%;
  top: 20%;
  width: 15%;
  height: 40%;
  display: none;
}
.card input[type=radio]:checked:after {
  display: block;
}
.card input[type=radio]:disabled:after {
  border-color: #f4f4f4;
}

