.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown .content {
  display: none;
  position: absolute;
  z-index: 2;
  background-color: #fff;
  padding: 10px;
  border: 1px solid #333;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.dropdown .content.show {
  display: block;
}

.pointer {
  cursor: pointer;
}

.menu {
  visibility: hidden;
  position: fixed;
  background-color: teal;
  word-wrap: unset;
  margin: 0;
  transition: 0.5s;
  opacity: 0;
  padding: 10px;
  border: red 1px dashed;
}
.menu.shown {
  opacity: 1;
  visibility: visible;
}

h2.active {
  border-bottom: orange 3px solid;
  color: white;
}

/** Guide CSS */
.dimScreen {
  display: none;
  position: fixed;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10;
}

#note {
  display: none;
  /*background-color: #FDFF47;*/
  background: #d8eef4;
  color: #000;
  position: absolute;
  z-index: 11;
  padding: 10px;
  min-width: 250px;
  box-shadow: rgba(255, 255, 255, 0.4) 0px 8px 24px;
  border-radius: 5px;
  border: 10px solid;
  border-image-slice: 1;
  border-width: 5px;
  border-image-source: linear-gradient(to left, #59c1d5, #abdbe7);
}
#note > div {
  margin-bottom: 20px;
}
#note button {
  cursor: pointer;
  font-size: 0.8em;
  float: right;
  background: #59c1d5;
  border-radius: 2px;
  border: 5px solid;
  border-image-slice: 1;
  border-width: 2px;
  border-image-source: linear-gradient(to left, #59c1d5, #abdbe7);
}
#note h3 {
  margin-top: 5px;
}

.guide-highlight {
  position: relative;
  background-color: #fff !important;
  z-index: 11;
  box-shadow: rgba(255, 255, 255, 0.4) 0px 8px 24px;
  border-radius: 2px;
  padding: 4px;
  color: #59c1d5 !important;
}
.guide-highlight i {
  background-color: #fff !important;
  color: #59c1d5 !important;
}