@import "coderr-variables.scss";

.table {
  table-layout: fixed;
  display: table;

  thead tr {
    background-color: $bg-secondary;

    th {
      text-align: left;
      padding-top: 5px;
      padding-bottom: 5px;
    }
  }

  &.striped tbody {
    tr:nth-child(even) {
      background-color: lighten($bg-primary, 25%);
    }
  }

  tbody tr td {

    padding-top: 5px;
    padding-bottom: 5px;
  }
}
